@import url(https://fonts.googleapis.com/css2?family=Arimo&family=Lato&display=swap);
html{touch-action:manipulation}html,body{font-family:"Lato",sans-serif;height:100%;display:flex;flex-direction:column;background:#f1edee;-webkit-user-select:none;-ms-user-select:none;user-select:none;-webkit-tap-highlight-color:transparent;-webkit-touch-callout:none}#root{flex:1 1;display:flex;flex-direction:column}.titlebar h1{font-family:"Arimo",sans-serif;text-align:center;text-transform:uppercase;margin:.65rem;padding:0}.App{display:flex;flex:1 1;flex-direction:column;margin-top:calc(1rem + env(safe-area-inset-top))}.btn{background:#3d5467;border:none;margin:1rem;padding:1rem;border-radius:5px;color:#fff}.btn:active{background:#2a3a47}@media(hover: hover){.btn:hover{background:#f0f}}fieldset{position:relative;margin:1rem;background-color:#3d5467;border:none;border-radius:5px;color:#fff}fieldset legend{text-transform:uppercase;font-family:"Arimo",sans-serif;font-size:10px;position:absolute;bottom:100%;background-color:inherit;padding:.25rem .5rem;color:currentColor;border-radius:5px 5px 0 0}.form-control{display:flex;flex-direction:column;background-color:#fff;position:relative;margin:2rem 0rem 0rem 0rem;border-radius:0 5px 5px 5px}.form-control:not(:last-child){margin-bottom:3rem}.form-control label{text-transform:uppercase;font-family:"Arimo",sans-serif;font-size:10px;position:absolute;bottom:100%;background-color:inherit;padding:.25rem .5rem;color:#3d5467;border-radius:5px 5px 0 0}.form-control input,.form-control select{border:none;padding:.25rem;margin:.25rem;outline:none}.unit-toggle{display:inline-flex;align-items:center;margin-top:.5rem;margin-left:auto}.unit-toggle label{font-size:12px;color:#fff}.unit-toggle input{margin-left:.5rem}.result-table{display:flex;flex-direction:column;background-color:#3d5467;margin:0 1rem 0 1rem;margin-top:auto;overflow:hidden;padding:.25rem;border-radius:5px}.result-table table{width:100%;border-collapse:collapse}.result-table table tr,.result-table table td{border:none}.result-table table td{color:#fff;padding:.25rem}.result-table table .text-center{text-align:center}.result-table table .text-right{text-align:right}.result-table table .header{font-family:"Arimo",sans-serif;font-weight:bold;color:#fff;padding:.25rem;font-size:12px}section.app-content{display:flex;flex-direction:column;flex:1 1;margin-bottom:calc(64px + env(safe-area-inset-bottom));padding-bottom:1rem}nav.app-menu{height:64px;background-color:#3d5467;position:fixed;bottom:0;width:100%;display:flex;flex-direction:row;justify-content:center;padding-bottom:env(safe-area-inset-bottom)}nav.app-menu button.menu-button{flex-basis:0;flex:1 1;display:flex;flex-direction:column;background:none;outline:none;border:none;border-bottom:4px solid transparent;justify-content:center;align-items:center;color:#fff}nav.app-menu button.menu-button img.icon{color:#fff}nav.app-menu button.menu-button.active{background-color:#2a3a47;border-color:#fff}.loading-page{display:flex;justify-content:center;align-items:center;flex:1 1}.loading-spinner{position:relative}.loading-spinner .spinner{width:128px;height:128px}.loading-spinner .spinner span{display:block;box-sizing:border-box;padding:.25rem;width:100%;height:100%;border:.5rem solid #3d5467;border-bottom-color:transparent;border-radius:256px;animation:loading-spinner 2500ms linear infinite}.loading-spinner span.spinner-text{position:absolute;top:calc(100% + 1rem);text-transform:uppercase;color:#3d5467;font-weight:bold;left:50%;transform:translateX(-50%)}@keyframes loading-spinner{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}section.about-page{height:100%;color:#333;padding:1rem}section.about-page h1{font-family:"Arimo",sans-serif}fieldset.language-switcher{display:flex;flex-direction:row;justify-content:space-between}fieldset.language-switcher button{flex:1 1;margin:.5rem;background:#eee;color:#3d5467;border:none;outline:none;min-height:64px;text-transform:uppercase}fieldset.language-switcher button.active{outline:4px solid #3d5467;outline-offset:-8px}a.spiffy-link{background-image:linear-gradient(45deg, #E67229, #d029e6, #294ce6);background-size:500%;-webkit-background-clip:text;background-clip:text;font-weight:bold;animation:bg-animation 45s infinite alternate;color:#fff;color:transparent}img.teeko-image{width:100%}@keyframes bg-animation{0%{background-position:left}100%{background-position:right}}.toggle-switch{position:relative;display:inline-block;width:42px;height:21px}.toggle-switch input{opacity:0;width:0;height:0}.toggle-switch .toggle-switch-slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#eee;border-radius:21px}.toggle-switch .toggle-switch-slider::before{position:absolute;content:"";width:13px;height:13px;top:4px;left:4px;background-color:#3d5467;border-radius:13px;transition:all 150ms ease-out}.toggle-switch input:checked+.toggle-switch-slider::before{transform:translateX(150%)}
